import { Component, Prop, h } from '@stencil/core';

export type FeaturedIconSize = 'sm' | 'md' | 'lg' | 'xl';
export type FeaturedIconColor = 'brand' | 'gray' | 'error' | 'warning' | 'success' | 'highlight';
export type FeaturedIconStyle = 'outlined' | 'filled';

@Component({
  tag: 'bh-featured-icon',
  styleUrl: 'bh-featured-icon.css',
  shadow: true,
})
export class BhFeaturedIcon {
  /**
   * The size of the featured icon
   */
  @Prop() size: FeaturedIconSize = 'md';

  /**
   * The color theme of the featured icon
   */
  @Prop() color: FeaturedIconColor = 'gray';

  /**
   * The style variant (outlined or filled)
   */
  @Prop() iconStyle: FeaturedIconStyle = 'outlined';

  /**
   * The Material Symbol icon name
   */
  @Prop() icon: string = 'star';

  render() {
    const containerClasses = {
      'featured-icon': true,
      [`featured-icon-${this.size}`]: true,
      [`featured-icon-${this.color}`]: true,
      [`featured-icon-${this.iconStyle}`]: true,
    };

    const iconClasses = `material-symbols-outlined icon-${this.size}`;

    return (
      <div class={containerClasses} part="container">
        <span class={iconClasses}>{this.icon}</span>
      </div>
    );
  }
}
